export const getLevelInfomation = (level) => {
  const { targetId } = ItemLevel[level];
  return {
    ...ItemLevel[level],
    image: ItemImage[targetId],
    name: ItemName[targetId],
    largeImage: ItemLargeImage[targetId],
    smallImage: ItemSmallImage[targetId]
  };
};

