import React from 'react';
import './index.scss';

class RulesPopup extends React.PureComponent {
  render() {
    if (!this.props.showPopup) {
      return null;
    }
    return (
      <div className="popup">
        <div className="popup_inner">
          <div className="header">
            <span className="title">Thể lệ chương trình</span>
            <button
              className="icclose_page_black"
              onClick={this.props.onClosePress}
            >
              <img
                className="image"
                src={require('./images/cancel@3x.png')}
                alt=""
              />
            </button>
          </div>
          <div className="content">
            <span className="normal">
              Mỗi người chơi có 1 lượt chơi, mỗi lượt chơi gồm 5 Vòng, mỗi vòng
              kéo dài 30 giây. Các món sẽ xuất hiện ngẫu nhiên và biến mất ở các
              vị trí, chọn đúng món bạn được +1 điểm, chọn nhầm món bạn sẽ bị -1
              điểm.
              <br />
              <br />
              <b>- Vòng 1:</b> Chọn bánh Big Mac
              <br />
              <b>- Vòng 2:</b> Chọn Khoai Tây Chiên
              <br />
              <b>- Vòng 3:</b> Chọn Gà Rán
              <br />
              <b>- Vòng 4:</b> Chọn Cơm Trứng Heo Nướng
              <br />
              <b>- Vòng 5:</b> Chọn Happy Meal Kết thúc
              <br /> <br />5 Vòng tương ứng với số điểm bạn được được sẽ nhận
              được những phần quà tương ứng. Hãy nhanh tay kiếm thật nhiều điểm
              để nhận được các phần quà có giá trị nhé. Chúc bạn chơi game thành
              công.
            </span>
          </div>
        </div>
      </div>
    );
  }
}

class HomePage extends React.PureComponent {
  state = {
    showPopup: false
  };
  handleClosePress = () => {
    this.setState({
      showPopup: false
    });
  };
  handleOpenPopup = () => {
    this.setState({
      showPopup: true
    });
  };

  render() {
    return (
      <div className="home-page">
        <button
          className="button"
          onClick={() => {
            const { history } = this.props;
            history.push('./play');
          }}
        >
          <span className="title">CHƠI GAME</span>
        </button>

        <button className="rules" onClick={this.handleOpenPopup}>
          <span className="title">Thể lệ chương trình</span>
        </button>
        <img
          className="firewall"
          src={require('./images/firewall@3x.png')}
          alt=""
        />
        <RulesPopup
          showPopup={this.state.showPopup}
          onClosePress={this.handleClosePress}
        />
      </div>
    );
  }
}

export default HomePage;
